.page-header .container {
  background-color: var(--header-background-color);
  border-color: var(--header-border-color);
  border: 1.5px solid;
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media (max-width: 767px) {
  .header-burger {
    width: 28px;
    height: 28px;
    stroke: currentColor;
    fill: currentColor;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    justify-content: center;
    align-items: center;
    margin-left: auto;
    margin-right: 0;
    padding: 0;
    display: flex;
    position: -webkit-sticky;
    position: sticky;
  }

  .header-menu {
    width: 60%;
    height: 100%;
    z-index: 100;
    background-color: var(--cards-background-color);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    padding: 24px 32px;
    transition: opacity .25s linear;
    position: fixed;
    top: 0;
    right: 0;
  }

  .header-menu .js-open-modal {
    opacity: 1;
    visibility: visible;
    pointer-events: initial;
  }

  .header-menu > .container-mob-header {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    display: flex;
    overflow-y: auto;
  }

  .header-mob-close {
    width: 24px;
    height: 24px;
    fill: var(--text-color-blond);
    background-color: rgba(0, 0, 0, 0);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    margin-bottom: 100px;
    margin-left: auto;
    display: flex;
  }

  .header-user, .site-nav-header {
    display: none;
  }

  .slider.round {
    border-radius: 34px;
  }

  .slider.round:before {
    border-radius: 50%;
  }

  .switch input {
    opacity: 0;
    width: 0;
    height: 0;
  }

  input:checked + .slider.round {
    background-color: var(--font-color-selected);
  }

  input:focus + .slider.round {
    box-shadow: 0 0 1px var(--font-color-selected);
  }

  input:checked + .slider.round:before {
    transform: translateX(26px);
  }
}

@media (min-width: 768px) {
  .site-nav-header {
    justify-content: center;
    align-items: center;
    margin-left: 72px;
    display: flex;
  }

  .site-nav-header .header-item + .header-item {
    margin-left: 24px;
  }

  .header-user {
    background-color: var(--font-color-selected);
    border-color: rgba(0, 0, 0, 0);
    border-radius: 36px;
  }

  .header-item {
    background-color: var(--header-background-color);
    border-color: rgba(0, 0, 0, 0);
    border-radius: 36px;
  }

  .header-item:hover, .header-item:focus {
    background: #eac645;
  }

  .header-nav-link {
    letter-spacing: -.56px;
    color: var(--font-color-dark);
    text-transform: uppercase;
    align-items: center;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.28571;
    text-decoration: none;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .header-to-link {
    letter-spacing: -.56px;
    color: var(--font-color-dark);
    text-transform: uppercase;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.28571;
    text-decoration: none;
    display: flex;
  }

  .header-user-link {
    color: var(--header-background-color);
    letter-spacing: -.44px;
    font-size: 22px;
    font-weight: 700;
    line-height: normal;
  }

  .header-menu, .header-burger, .header-mob-menu {
    display: none;
  }
}



